import React from 'react';
import './shoppingcart.css';

import DB from '../db';

class ShoppingCart extends React.Component {
  constructor(props) {
    super(props)
    this.User = null;
    this.state = {
      cart: []
    }
  }

  componentDidMount() {
    this.setUserName();
  }

  setUserName() {
    this.User = require('../user').default;
    this.setState({
      cart: this.User.getCartItems()
    });
  }

  render() {
    let cart = this.state.cart.slice();
    let items = cart.map(item => {
      return (<li className="shoppingcart-item list-group-item d-flex justify-content-between lh-condensed">
        <div>
          <h6 className="my-0">{DB[item.catId].subProducts[item.prodId].name}</h6>
        </div>
        <span className="text-muted">${DB[item.catId].subProducts[item.prodId].price}</span>
      </li>
    )});

    return (
    <div className="container mt-5">
      <div className="row">
        <div className="col-lg-8 offset-lg-2">
          <h1>Shopping Cart</h1>

          <ul className="list-group mb-3">
            {items}
          </ul>
        </div>
      </div>
    </div>
    )
  }
}

export default ShoppingCart;
